

// shop stuff on frontpage

.product-section-container {
	text-align: center;
    width: 100%;
    padding-bottom: 4.5rem;
    

	.product-block-wrapper {
		display: flex;
        flex-direction: row;
		width: $base__width;
        margin: auto;
        justify-content: space-between;
		.product-block-single {
            

            width: 20rem;
			height: 16rem;
			padding: 0.75em;
            flex: 0 1 23.5%;
			border: 1px solid $colar__inhab-bordercolor;
            border-top-width: .5rem;           
                &:hover{
                    border: 1px solid $color__inhab-orange;
                    border-top-width: .5rem;  
                }
			a {
                text-decoration: none;
                background-color: $color__inhab-green;
                color: $color__background-body;
                padding: .75rem 1.5rem;
            }
            
            img {
                height: 4.375rem; 
                width: 4.375rem;
            }
		}	
	}
}


// fronst page journel css

.journal-content-area {
    text-align: center;
    width: $base__width;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 4.5rem;

    ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;

        li {
            flex: 0 1 32%;
            border: 1px solid $colar__inhab-bordercolor;
            position: relative;
            img {
                border-bottom: 3px solid $colar__inhab-bordercolor;
                width: 100%;
                height: 250px;
            }
            div {
                padding: .75rem;
                display: flex;
                flex-direction: column;
                align-items: baseline;
                justify-content: space-between;
                text-align: left;
                .front-page-journal-title {
                    font-size: $font__size * 1.4;
                    margin-bottom: 3.75rem;
                    margin-top: 0;
                    font-family: $font__secondary,sans-serif;
                }
                .front-page-journal-btn {
                    color: black;
                    border: 1px solid black;
                    position: absolute;
                    bottom: .75rem;
                    left: .75rem;
                    padding: .75rem 1.5rem;
                }
        
            }
            
        }
    }
}

//front page adventure css

.home{
    .adventures-hardcode{
        margin: 0 auto 3rem auto;
        width: $base__width;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 1fr;
        grid-gap: 20px;
        .adventures-left-big{
            background-image: url("../../images/adventure-photos/canoe-girl.jpg");
            background-size: cover;
            background-repeat: no-repeat;
            background-position: 50% 50%;
            height: 550px;
            grid-row: 1 / span 2;
            grid-column: 1 / span 2;
        }
        .adventures-Top-right{
            background-image: url("../../images/adventure-photos/beach-bonfire.jpg");
            grid-column: 3 / span 2;
            grid-row: 1 / span 1;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: 50% 50%;
            height: 265px;
        }
        .adventures-bottom-right-a{
            background-image: url("../../images/adventure-photos/mountain-hikers.jpg");
            background-size: cover;
            background-repeat: no-repeat;
            background-position: 50% 50%;
            height: 265px;

        }
        .adventures-bottom-right-b{
            background-image: url("../../images/adventure-photos/night-sky.jpg");
            background-size: cover;
            background-repeat: no-repeat;
            background-position: 50% 50%;
            height: 265px;

        }
    }
}

// Shops CSS
.products-content-area-wrapper{
    padding-top: 5rem;
    padding-bottom: 2.5rem ;
    font-family: $font__secondary;

    .product-type-container{
        text-align: center;
        text-transform: uppercase;

        .shop-page-title{
            font-size: $font__size * 2.5;
        }
        .product-term{
            display: flex;
            font-size: $font__size * 1.2;
            justify-content: center;   
            padding-bottom: 3rem;         
            a {
                margin: 0 2rem;
            }
        }
    }
    .product-grid-display {
        margin-left: auto;
        margin-right: auto;
        width: $base__width;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(4, 1fr);
        
        grid-gap: 1rem;
        padding: 3rem 0;
        border-top: 1px dashed darken($colar__inhab-bordercolor, 20%);
        .product-item{
            border: 1px solid $colar__inhab-bordercolor;
            .item-image-container{
                background-color: $colar__inhab-bordercolor;
                .item-image-link{
                    .wp-post-image {
                        height: 280px;
                        width: 100%;
                        object-fit: cover;
                    }
                }
            } 
            .item-info{
                padding: 1rem;
                .item-title{
                    display: inline;
                    background: $color__background-body;
                    &:before {
                        float: left;
                        width: 0;
                        white-space: nowrap;
                        content: '...................................................';
                    }
                    }
                
                .item-price{
                    display: inline;
                    float: right;
                    background: $color__background-body;
                    
                }
            }
        }
    }
}

